@media screen and (max-width: 1200px), screen and (max-device-width: 1200px) {
  body {
    font-size: 14px;
    line-height: 16px;
  }
  body .header__headline .container .row .col-md-12 p {
    font-size: 8.3em;
  }
  body .header__headline .container .row .col-md-6 .logo a {
    width: 285px;
  }
  body .header__headline .container .row .col-md-6 .logo div p:nth-child(1) {
    font-size: 1em;
  }
  body .header__headline .container .row .col-md-6 .logo div p:nth-child(2) {
    font-size: 1.24em;
  }
}
@media screen and (max-width: 992px), screen and (max-device-width: 992px) {
  body {
    font-size: 16px;
    line-height: 18px;
  }
  body .header__headline {
    height: 105px;
  }
  body .header__headline .container .row .col-sm-12 p {
    font-size: 5.6em;
    line-height: 105px;
  }
  body .header__headline .container .row .col-sm-6 .logo,
  body .header__headline .container .row .col-sm-6 .search {
    height: 105px;
    top: -105px;
  }
  body .header__headline .container .row .col-sm-6 .logo a {
    height: 105px;
  }
  body .header__mainNav .container .row .col-sm-12 ul li a span:nth-child(2) {
    font-size: .688em;
  }
}
@media screen and (max-width: 767px), screen and (max-device-width: 767px) {
  body .header__nav .container .row .col-xs-1 .nav {
    display: none;
  }
  body .header__nav .container .row .col-xs-1 .mobile__nav {
    display: block;
  }
  body .header__headline .container .row .col-xs-12 p {
    font-size: 5em;
  }
  body .header__headline .container .row .col-sm-6 .search {
    display: none;
  }
  body .header__mainNav .container .row .col-xs-12 .mainNav {
    display: none;
  }
  body .header__mainNav .container .row .col-xs-12 .mobile__mainNav {
    display: flex;
  }
}
@media screen and (max-width: 690px), screen and (max-device-width: 690px) {
  body .header__headline .container .row .col-xs-12 p {
    display: none;
  }
  body .header__headline .container .row .col-xs-12 .logo {
    position: unset;
  }
  body .header__headline .container .row .col-xs-12 .logo div p {
    display: block;
  }
}
